import { Component, Input, OnInit } from '@angular/core';

import { NbMenuService, NbSidebarService } from '@nebular/theme';
import { UserService } from '../../../@core/data/users.service';
import { AuthService} from "../../../auth/auth.service"
import { AnalyticsService } from '../../../@core/utils/analytics.service';
import {Http} from "@angular/http"
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'ngx-header',
  styleUrls: ['./header.component.scss'],
  templateUrl: './header.component.html',
})
export class HeaderComponent implements OnInit {


  @Input() position = 'normal';

  user: any;
  project:any;
  projectsMenu: Array<any>;

  userMenu = [ { title: '退出'}];

  constructor(private sidebarService: NbSidebarService,
              private menuService: NbMenuService,
              private userService: UserService,
              private http : Http,
              private analyticsService: AnalyticsService,
              private authService: AuthService,
              private r: ActivatedRoute
            ) {
  }

  ngOnInit() {
    this.authService.getUserOb()
      .subscribe((user: any) => {
        this.user = user
        if (this.user["type"]=="普通用户"){
         setTimeout(()=>{
           this.toggleSidebar()
         }, 500)
        }else if(this.user["type"]=="管理员"){
        }
      });
      this.authService.getProjectOb()
      .subscribe((projects:any)=>{
        this.projectsMenu = projects;
        let index = this.authService.getIndex()
        if(this.projectsMenu.length>0){
          this.project = this.projectsMenu[index]
          this.project.name = "项目：" + this.project.name
          console.log(this.project)
        }
      })
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    console.log("clicl")
    return false;
  }

  toggleSettings(): boolean {
    this.sidebarService.toggle(false, 'settings-sidebar');
    return false;
  }

  goToHome() {
    this.menuService.navigateHome();
  }

  startSearch() {
    this.analyticsService.trackEvent('startSearch');
  }

  onMenuItemClick(e){
    if(e['title']=="退出"){
      
      this.http.get("/api/logout").toPromise().then((res)=>
      {
        window.location.href="/login.html"
      }
      ).catch(
        (res)=>
        {
          window.location.href="/login.html"
          console.log(res)
        }
      )
    }
    console.log(e)
  }
  getProjects(){

  }
  changeProject(event){
    console.log(event)
  }
}



