import {Http} from "@angular/http"
import { promise } from "selenium-webdriver";
import { Injectable } from '@angular/core';
import {CanActivate, ActivatedRouteSnapshot, }from "@angular/router"
import { Observable } from 'rxjs/Observable';
import { RouterStateSnapshot, Router } from '@angular/router';
import { Project } from "../@core/data/models";
import { DefaultEditor } from "ng2-smart-table/components/cell/cell-editors/default-editor";

export let USER: any
export let PROJECTS: Array<any>
export let DefaultProjectIndex: number = 0


@Injectable()
export class AuthService {
    constructor(private http: Http, private route: Router){}
    private user: any
    private role: string
    getRole(){
        return this.role
    }
    init() {
       this.getUser().then(
           (res)=>{
               this.user = res.json()
               USER = this.user
               // fixme 
               PROJECTS = USER["projects"]
               let role = this.getRol()
               if(role=="普通用户"){
                   this.route.navigateByUrl("/pages/common-user/index")
               }

           }
       ).catch(
           (res)=>{
               this.goToLogin()
           }
       )
       
       return this.user
    }
    getUser():Promise<any>{
        return this.http.get("/api/user/v1/logined").toPromise()
    }
    getUserOb():Observable<any> {
        return Observable.of(USER);
      }
    getProjectOb():Observable<any> {
        return Observable.of(PROJECTS);
    }
    getIndex(){
        return DefaultProjectIndex
    }
    setIndex(i){
        DefaultProjectIndex = 1
    }
    getCurrentProjectID(){
        let p = PROJECTS[DefaultProjectIndex]
        if (!p){
           this.goToLogin() 
        }
        return p['id']
    }
    getRol(){
        if(this.user==null){
            this.goToLogin()
        }

        this.role = this.user["type"]
        if(this.user["type"]=="管理员"){
           return this.role 
        }
        else if(this.user["type"]=="普通用户"){
            if(this.user['projects'].length<=0){
            alert("对不起，您没有被添加到任何项目，请联系管理员")
                this.goToLogin()
            }
            
           return this.role 
        }
        else if(this.user["type"]=="运维用户"){
            if(this.user['projects'].length<=0){
            alert("对不起，您没有被添加到任何项目，请联系管理员")
                this.goToLogin()
            }
           return this.role 
        }
        else{
            this.goToLogin()
        }

    }
    goToLogin(){
        console.log("redrict")
        window.location.href="/login.html"
    }
}

// @Injectable()
export class UserToken {}
export class Permissions {
//   constructor(public auth: AuthService){}
  canActivate(user: UserToken, id: string): boolean {
    return true;
  }

}
 
@Injectable()
export class CanActivateTeam implements CanActivate {
  constructor(private permissions: Permissions, private currentUser: UserToken) {}
 
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean>|Promise<boolean>|boolean {
    return this.permissions.canActivate(this.currentUser, route.params.id);
  }
}


